-->
<template>
  <el-dialog title="提示" :visible.sync="visible" width="25%" class="tipDialog" :before-close="closeDialog">
    <div v-if="dialogType==='input'" style="width:80%;margin-left:10%">
      <div style="font-size:16px;margin-bottom: 20px;">{{ toolTips?toolTips:'理由' }}： </div>
      <el-input :value="reason" type="textarea" :autosize="{ minRows: 3, maxRows: 5}" placeholder="请输入内容" @input="updateReason" />
    </div>
    <div v-else-if="dialogType==='tips'">
      <i style="color:#e6a23c;font-size: 18px;" class="el-icon-warning" /> {{ toolTips?toolTips:'是否继续进行当前操作' }}
    </div>
    <div v-else>
      <i style="color:#e6a23c;font-size: 18px;" class="el-icon-warning" /> 是否继续进行当前操作
    </div>
    <div v-if="btnParam&&btnParam.length>0" slot="footer" class="dialog-footer">
      <el-button v-for="(item,index) in btnParam " :key="index" plain :type="item.type" size="medium" @click="affirm(item.btnlable)">{{ item.btnlable }}</el-button>
    </div>
    <div v-else slot="footer" class="dialog-footer">
      <el-button size="small" @click="closeDialog">取 消</el-button>
      <el-button type="primary" size="small" @click="affirm('affirm')">确 定</el-button>
    </div>
  </el-dialog>
</template>
<script>
// this.tipLabel = { show: true, type: 'tips', BtnPram: [{ btnlable: '审核', type: 'danger' }, { btnlable: '修改', type: 'success' }] }
// this.tipLabel = { show: true, type: 'input', BtnPram: [{ btnlable: '驳回', type: 'danger' }, { btnlable: '通过', type: 'primary' }] }
// this.tipLabel = { show: true, type: 'tips',msg:"这是提示" }
export default {
  name: 'MessageDialog',
  model: {
    prop: 'reason',
    event: 'updateReason', // 随便命名事件，对应下面$emit即可
  },
  props: {
    title: { // 弹框标题
      type: String,
      default: function() {
        return ''
      }
    },
    reason: { // 理由 可使用v-model
      type: String,
      default: function() {
        return ''
      }
    },
    visible: { // 是否打开弹窗
      type: Boolean,
      default: function() {
        return false;
      }
    },
    toolTips: { // 原因提示或删除提示
      type: String,
      default: function() {
        return ''
      }
    },
    dialogType: { // 弹框类型 原因tips 输入框input
      type: String,
      default: function() {
        return 'tips'
      }
    },
    btnParam: { // 自定义按钮
      type: Array,
      default: function() {
        return []
      }
    }
  },
  data() {
    return {
    }
  },

  mounted() {
  },
  methods: {
    affirm(flag) {
      this.$emit('affirm', flag, this.reason)
    },
    updateReason(value) {
      this.$emit('updateReason', value)
    },
    closeDialog() {
      this.$emit('update:visible', false)
    },
  }
}
</script>

<style lang="scss" scoped>
.tipDialog {
  ::v-deep.el-dialog {
    min-width: 400px;
    border-radius: 7px;
  }
  ::v-deep .el-dialog__header {
    padding: 10px 20px;
    border-bottom: 1px solid #eee;
    .el-dialog__headerbtn {
      position: absolute;
      top: 10px;
      right: 20px;
    }
  }
  ::v-deep .el-dialog__body {
    padding: 20px;
  }
  ::v-deep .el-dialog__footer {
    text-align: right;
  }
}
</style>
